import "./App.css";

import Storyfinal from "./comp/Storyfinal";

import {
  RouterProvider,
  createBrowserRouter ,
  createHashRouter as router,
  Navigate,
} from "react-router-dom";
import Rootlayout from "./comp/Rootlayout";
import Hometest from "./comp/Hometest";
// import {
//   BrowserRouter as Router,
//   Routes,
//   Route,
//   Navigate,
// } from "react-router-dom";
import ReactGA from 'react-ga';
const TRACKING_ID = 'UA-252963568-4'
  ReactGA.initialize(TRACKING_ID);
const router1 = router([
  {
    path: "/",
    element: <Rootlayout />,
    children: [
      { index: true, element: <Hometest /> },
      { path: "/stories/:id", element: <Storyfinal /> },
      { path: "*", element: <Navigate to={"/"} /> },
    ],
  },
]);

function App() {
  return (
    <>
    <RouterProvider router={router1} />;
    </>
  );
}

export default App;

