import React, { useContext, useEffect, useState } from "react";
import { WithSeeMore } from "react-insta-stories";
import Testtt from "./Testtt";
import { Apicontext } from "./Apicontext";
import Stories from "react-insta-stories";
import ReactGA from 'react-ga';

import clasess from "./Storyfinal.module.css";

function Storyfinal() {
  let { terdMovie } = useContext(Apicontext);
  const storyHeight = "92vh";
  const [currentId, setCurrentId] = useState(0);
  const customSeeMore = {
    textAlign: "center",
    fontSize: 14,
    bottom: 20,
    position: "relative",
    marginLeft: "35%",
  };

  let x = {};
  if (terdMovie.length > 0) {
    console.log(terdMovie[0].media);
    x = {
      content: ({ action, story }) => {
        return (
          <WithSeeMore
            className=" position-relative"
            story={story}
            action={action}
          >
            <div style={{ background: "pink", padding: 0 }}>
              <img className=" img-fluid" src={terdMovie[0].media} />
              {/* <h1 className=" position-absolute bottom-0" style={{ marginBottom: "0%", marginBottom: 100 }}>🌝</h1> */}
              <h1 className={clasess.testrender}>{terdMovie[0].content}</h1>
            </div>
          </WithSeeMore>
        );
      },
      seeMoreCollapsed: ({ toggleMore, action }) => (
        <p
          className=" btn btn-light  "
          style={customSeeMore}
          onClick={() => {
            action("pause");
            window.open(terdMovie[0].see_more, "_blank");
          }}
        >
          {" "}
          See More
        </p>
      ),
      seeMore: ({ close }) => (
        <div
          style={{
            maxWidth: "100%",
            height: "100%",
            padding: 40,
            background: "white",
          }}
        >
          <h2>Just checking the see more feature.</h2>
          <p style={{ textDecoration: "underline" }} onClick={close}>
            Go on, close this popup.
          </p>
        </div>
      ),
    };
  }

  // const fTest = useMemo(() => x, [fTest]);

  const fTest = [x];

  // /* <h1 className=" position-absolute bottom-0" style={{ marginBottom: "0%", marginBottom: 100 }}>🌝</h1> */
  if (terdMovie.length > 0 && fTest !== terdMovie.length) {
    for (let index = 0; index < terdMovie.length; index++) {
      // console.log(terdMovie[index].media);
      // navigate(`/${index}`);
      fTest.push({
        content: ({ action, story }) => {
          return (
            <WithSeeMore
              className=" position-relative"
              story={story}
              action={action}
            >
              <div>
                <h1
                  className={clasess.testrender}
                  // style={{ marginBottom: 100 }}
                >
                  {terdMovie[index].content}
                </h1>
                <img className=" img-fluid" src={terdMovie[index].media} />
              </div>
            </WithSeeMore>
          );
        },
        seeMoreCollapsed: ({ toggleMore, action }) => (
          <p
            className=" btn btn-light  "
            style={customSeeMore}
            onClick={() => {
              action("pause");
              window.open(terdMovie[index].see_more, "_blank");
            }}
          >
            See More{" "}
          </p>
        ),
        seeMore: ({ close }) => (
          <div
            style={{
              maxWidth: "100%",
              height: "100%",
              padding: 40,
              background: "white",
            }}
          >
            <h2>Just checking the see more feature.</h2>
            <p style={{ textDecoration: "underline" }} onClick={close}>
              Go on, close this popup.
            </p>
          </div>
        ),
      });
    }
  } else {
    return;
  }
 
  // useEffect(() => {
  //   ReactGA.pageview(window.location.pathname + window.location.search);
  // })

  return (
    <>
      <Stories
        currentIndex={currentId}
        loop
        width={432}
        height={storyHeight}
        keyboardNavigation
        defaultInterval={8000}
        stories={fTest}
        onStoryEnd={(s, st) => {
          console.log("story ended", s, st);
          setCurrentId((currentId) => currentId + 1);
          console.log("story ended", currentId);
          ReactGA.pageview(window.location.pathname + window.location.search+`"post 1 / slide ${currentId} end"`);

        }}
        onAllStoriesEnd={(s, st) => {
          console.log("all stories ended", s, st);
          setCurrentId((currentId) => 0);
          ReactGA.pageview(window.location.pathname + window.location.search+`"post 1 /  all stories end "`);

        }}
        onStoryStart={(s, st) => {
          console.log("story started", s, st);
          setCurrentId((currentId) => currentId + 1 - 1);
          console.log("story start", currentId);
          ReactGA.pageview(window.location.pathname + window.location.search+`"post 1 / slide ${currentId} start"`);

        }}
      />{" "}
    </>
  );
}

export default Storyfinal;

